import * as Yup from 'yup';

export const validationDelivery = () => {

  return(
    Yup.object({
      deliveryMethod: Yup.string(),
      phone: Yup.string()
               .matches(/\d/gm, " ")
               .matches(/^\+375\([25,29,33,44]/, ' ')
               .max(15, ' 9   ')
               .min(15, ' 9   ')
               .required('   '),
      email: Yup.string()
              .email(' email ')
              .matches(/\.[a-z, A-Z]{2,63}$/, ' ')
              .required('   '),
      country: Yup.string()
              .matches(/\D/gm, " ")
              .required('   '),
      city: Yup.string()
              .when('deliveryMethod', {
                is: (deliveryMethod) => (deliveryMethod === 'pickup from post offices' || deliveryMethod === 'express delivery'),
                then: Yup.string().matches(/\D/gm, " ").required('   ') }),
      street: Yup.string()
              .when('deliveryMethod', {
                is: (deliveryMethod) => (deliveryMethod === 'pickup from post offices' || deliveryMethod === 'express delivery'),
                then: Yup.string().matches(/\D/gm, " ").required('   ') }),
      house: Yup.string()
            .when('deliveryMethod', {
              is: (deliveryMethod) => (deliveryMethod === 'pickup from post offices' || deliveryMethod === 'express delivery'),
              then: Yup.string().required('   ') }),
      postcode: Yup.string()
                  .when('deliveryMethod', {
                  is: 'pickup from post offices',
                  then: Yup.string().matches(/[0-1]/gm, " ").required('   ') }),
      storeAddress: Yup.string()
                    .when('deliveryMethod', {
                      is: 'store pickup',
                      then: Yup.string().required('   ')
                                        .max(24, '  ') }),
      terms: Yup.boolean()
              .required(' ')
              .oneOf([true], " ") })
  )
};


export const validationPayment = () => {

  return(
    Yup.object({
        
      card: Yup.string()
        .when('paymentMethod', {
          is: paymentMethod => (paymentMethod === 'visa' || paymentMethod === 'master'),
          then: Yup.string()
                .matches(/\d/gm, "   ")
                .length(16, '   ')
                .required('   '),
          }),   

      cardDate:Yup.string()
        .when('paymentMethod', {
            is: paymentMethod => (paymentMethod === 'visa' || paymentMethod === 'master'),
            then: Yup.string()
              .matches(/(([0]{1}[1-9]{1}|[1]{1}[1-2]{1}).[2-4]{1}[3-9]{1})|(([0]{1}[4-9]{1}|[1]{1}[1-2]{1}).[2]{1}[2]{1})/, " ")
              .required('   '),
          }),

      cardCVV:Yup.string()
        .when('paymentMethod', {
            is: paymentMethod => (paymentMethod === 'visa' || paymentMethod === 'master'),
            then: Yup.string()
                  .matches(/^[0-9,*]+$/, ' ')
                  .required('   '),
          }),   
              
      cashEmail:Yup.string()
          .when('paymentMethod', {
            is: 'PayPall',
            then: Yup.string()
                      .email(' email ')
                      .matches(/\.[a-z, A-Z]{2,63}$/, ' ')
                      .required('   '),
          }),
    }))
}